
.bubble {
  width: 10px;
  height: 10px;
  position: absolute;

  > div {
    width: 100%;
    height: 100%;
    position: absolute;

    > div {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      position: absolute;
      background: rgba(255, 221, 0, 0.8);
      box-shadow: rgba(224, 249, 255, 0.42) 2px 0px 0px inset;
    }
  }


  &.x1 {
    transform: scale(0.9);
    opacity: 1;
    left: 10vw;
    top: 50vw;

    > div {
      animation: moveclouds 3s linear infinite;

      > div {
        animation: sideWays 1.5s ease-in-out infinite alternate;
      }
    }

  }

  &.x2 {
    left: 150px;
    transform: scale(0.6);
    opacity: 0.75;
    top: 50vw;

    > div {
      animation: moveclouds 3s linear infinite;

      > div {
        animation: sideWays 1.5s ease-in-out infinite alternate;
      }
    }
  }

  &.x3 {
    left: 200px;
    top: 50vw;
    transform: scale(0.8);
    opacity: 0.6;
    >div {
      animation: moveclouds 6s linear infinite;
      > div {
      animation:  sideWays 2s ease-in-out infinite alternate;
      }
    }
  }

  &.x4 {
    left: 250px;
    transform: scale(0.75);
    opacity: 0.5;
    top: 50vw;
> div {
  animation: moveclouds 9s linear infinite;
  >div {
    animation: sideWays 1s ease-in-out infinite alternate;;
  }
}
  }

  &.x5 {
    left: 300px;
    transform: scale(0.8);
    opacity: 0.9;
    top: 50vw;
    >div {
      animation: moveclouds 4s linear infinite;
      >div {
        animation: sideWays 0.5s ease-in-out infinite alternate;
      }
    }

  }

  &.x6 {
    left: 350px;
    transform: scale(0.9);
    opacity: 1;
    top: 50vw;
    > div {
      animation: moveclouds 1s linear infinite;
      > div {
        animation: sideWays 1.5s ease-in-out infinite alternate;
      }
    }
  }

  &.x7 {
    left: 400px;
    transform: scale(0.6);
    opacity: 0.75;
    top: 50vw;
   > div {
     animation: moveclouds 3s linear infinite;
     >div {
       animation: sideWays 1.5s ease-in-out infinite alternate;
     }
   }
  }

  &.x8 {
    left: 450px;
    transform: scale(0.8);
    opacity: 0.6;
    top: 50vw;
    > div {
      animation: moveclouds 6s linear infinite;
      >div {
        animation: sideWays 2s ease-in-out infinite alternate;
      }
    }
  }

  &.x9 {
    left: 470px;
    transform: scale(0.75);
    opacity: 0.5;
    top: 50vw;
    > div {
      animation: moveclouds 10s linear infinite;
      >div {
        animation: sideWays 1s ease-in-out infinite alternate;
      }
    }
  }

  &.x10 {
    left: 550px;
    transform: scale(0.8);
    opacity: 0.9;
    top: 50vw;
    > div {
      animation: moveclouds 5s linear infinite;
      >div {
        animation: sideWays .5s ease-in-out infinite alternate;
      }
    }
  }
}

@keyframes moveclouds {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0, -100vh);
  }
}

@keyframes sideWays {
  0% {
    transform: translate(0%, 0);
  }
  100% {
    margin-left: -30%;
  }
}
